import './example.css';
import React, { useState } from 'react';
import { message, Button,Modal } from 'antd';
import 'antd/dist/antd.css';
const TYPE = {
  URL:0,
  MSG:1,
  OTHER:2
};
const info = (msg) => {
  message.info(msg);
};

class Lists extends React.Component{
  state={
    avatar:"https://img1.baidu.com/it/u=1925715390,133119052&fm=253&fmt=auto&app=138&f=png?w=400&h=400",
    urlLists:[
      {
        url:"https://oec.pgo.farm/#/",
        img:require("../../imgs/vue.png"),
        txt1:"Vue",
        txt2:"手机H5卡牌游戏",

      },
      {
        url:"https://www.scoreupup.com/#/home",
        img:require("../../imgs/vue.png"),
        txt1:"Vue",
        txt2:"杀G工厂",

      },
      
      {
        url:"http://ceis.imlimit.com/#/",
        img:require("../../imgs/vue.png"),
        txt1:"Vue",
        txt2:"CEIS(响应式)",

      },
      {
        url:"",
        img:require("../../imgs/w.png"),
        txt1:"uni-app",
        txt2:"骑士邦",
        type:TYPE.MSG,
        msg:"微信小程序搜索“骑士邦”即可查看"

      },
      {
        url:"",
        img:require("../../imgs/react.png"),
        txt1:"React",
        txt2:"当前网页(响应式)",
        type:TYPE.MSG,
        msg:"当前网页就是使用react所写"

      },
      {
        url:"",
        img:require("../../imgs/no.png"),
        txt1:"其他",
        txt2:"未上线项目",
        type:TYPE.OTHER,
        msg:"当前网页就是使用react所写"

      },
      // {
      //   url:"http://bluedreamjeff.gitee.io/bluedreamjeff.github.io/as/dist/index.html#/index",
      //   img:require("../../imgs/vue.png"),
      //   txt1:"Vue",
      //   txt2:"AS官网(响应式)",

      // },
      // {
      //   url:"http://bluedreamjeff.gitee.io/bluedreamjeff.github.io/polyme/dist/index.html#/",
      //   img:require("../../imgs/vue.png"),
      //   txt1:"Vue",
      //   txt2:"polyme官网(响应式)",

      // },
    ],
    isModalVisible:false

  };
  navUrl=(url,type=TYPE.URL,msg="")=>{
    if(type==TYPE.URL)
    window.open(url); 
    else if(type==TYPE.MSG){
      info(msg);

    }
    else{
      this.setState({
        isModalVisible:true
      });
      console.log(this.state.isModalVisible)
    }

  }
  showModal = () => {
    this.setState({
      isModalVisible:true
    })
  };

  handleOk = () => {
    this.setState({
      isModalVisible:false
    })
  };

  handleCancel = () => {
    this.setState({
      isModalVisible:false
    })
  };
  render(){

    let items = [];

    this.state.urlLists.forEach((item,index)=>{
      let ele = <li key={index} onClick={()=>this.navUrl(item.url,item.type,item.msg)}>
      <p className="img">
        <img src={item.img} alt=""  />

      </p>
      <div className="txt">
          <p>{item.txt1}</p>
          <p>{item.txt2}</p>
      </div>
    </li>;
    items.push(ele);

    })


    return(
      <ul className="items">
          {items}
          <li>
          <Modal title="其他项目" visible={this.state.isModalVisible} onOk={this.handleOk} onCancel={this.handleCancel}>
            <p>包含了一些响应式的官网，因为计划改变的原因未能上线。</p>
            <p>以及使用uniapp开发过一个APP，有短信验证，邮箱验证功能。</p>
            <p>另外本人还独自完成过2个vue-element-admin的后台，包含一些动态权限，不同角色不同页面的访问，不过由于账号密码被修改，以及上线地址不详，因此无法展示。</p>
          </Modal>
          </li>
        </ul>
    )

  }
  
}



